@import url('http://fonts.cdnfonts.com/css/chomsky');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'monoscape';
  text-align: center;
}

h1 {
  border-top: 2px solid black;
  border-bottom: 2px solid black;

  font-family: 'Chomsky', sans-serif;
  font-weight: 400;
  font-size: 6.25rem;
  line-height: 8rem;
  
  margin-bottom: 15px;
}

.log-header {
  padding: 4px;
  border: solid black;
  border-width: 2px 0;
  margin: 2px;
  display: flex;
}

.log-header span {
  margin-right: 40px;
}

.log-header span:before {
  content: '';
  width: 14px;
  height: 14px;
  display: inline-block;
  margin-right: 10px;
  background: black;
}

.App {
  height: 100vh;
  width: 66vw;
  max-width: 1200px;
  margin: 0 auto;
  padding: 15px 0;
}


.panel-1 {
  float: left;
  overflow: hidden;
  width: 20%;
  margin-right: 3%;
}

.panel-2 {
  float: right;
  width: 77%;
}

.content {  
  height: 100%;
  width: 100%;
}

#nav {
  height: 15%;
  width: 100%;
  display: inline-block;
}

#nav ul {
  border-bottom: 1px grey solid;
}

#nav ul li {
  font-size: 1rem;
  list-style: none;
  width: 100%;
  text-align: left;
  border-top: 1px grey solid;
  padding: 5px;
}

#nav ul li .fas {
  min-width: 16px;
}

a {
  text-decoration: none;
  color: black;
}

li p {
  display: inline;
  margin-left: 15px;
}

#nav ul li:hover {
  background-color: black;
  color: white;
}

#notebook {
  position: absolute;
  right: 0;
  top: 0;
  width: 200px;
  background: white;
}

#Cluedo {
  height: 78vh;
}


/****************/
/* file styling */
/****************/

.file {
  float: right;
  margin-left: 5%;
  width: 70%;
  height: 100%;
}


.file h2 {
  background: black;
  color: white;
  padding: 10px 0;
  margin-bottom: 15px;
  font-size: 1.8em;
}


/********************/
/* notities styling */
/********************/

.notes {
  border: solid 2px black;
  transform: scale(.95);  
}

.notes h2 {
  margin: 0;
  width: 100%;
  padding: 3px;
  background-color: black;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.notes h3 {
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  margin: 4px 4px 10px;
}

.notes li {
  list-style: none;
  padding: 2px 0;
  margin-bottom: 4px;
}

.notes li:last-child {
  margin-bottom: 15px;
}

.notes span:hover {
  cursor: pointer;
}

.notes .disproven {
  color: lightgray;
}

.notes-btn {
  background: black;
  border: none;
  border-radius: 5px 5px 0 0;
  color: #fff;
  cursor: pointer;
  display: block;
  font-size: 15px;
  font-family: inherit;
  margin: 5px auto;
  padding: 10px 20px;
  position: absolute;
  right: -75px;
  top: calc(50% - 75px);
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 2px;
  transform: rotate(-90deg);
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
  width: 200px;
  height: 50px;
}

/*******************/
/** styling Manor **/
/*******************/

.row {
  width: 100%;
  height: calc(100%/3);
}

.post-form, .cluedo-btn-box {
  margin: 20px -10px 0 -10px;
  display: flex;
}

.post-form, .cluedo-btn-box button {
  margin: 0 10px;
}

/** Styling Suggestion Log */

.log-container {
  float: left;
  margin-top: 30px;
  width: 100%;
}

.log-new {
  height: 70px;
  width: 100%;
  padding: 5px;
}

.log-old {
  height: 250px;
  padding: 5px;
  overflow: auto;
}

.entry {
  overflow: hidden;
  text-align: left;
  background: #f4f4f4;
  margin: 5px;
  padding: 5px 10px;
  height: 50px;
}

.log-old .entry {
  margin-bottom: 10px;
}


.entry h3 {
  font-size: 1em;
  text-align: left;
  display: flex;
  align-content: center;
}

.entry div {
  text-align: left;
  display: flex;
  align-content: center;
}

.room-container {
  background-color: rgba(0,0,0,.2);
  display:inline-block;
  overflow: hidden;
  width: calc(100%/2);
  height: 100%;
}

.room-tile {
  display: flex;
  align-items: center;
  justify-content: center;

  width: calc(100% - 30px);
  height: calc(100% - 30px);
  margin: 15px;

  font-family: 'Chomsky', sans-serif;
  font-size: 2rem;
  color: aliceblue;
  background-color: black;
}

.room-tile:hover {
  margin: 0%;
  width: 100%;
  height: 100%;
  cursor: pointer;

  font-size: 2.5rem;
}

/** Suggestion styling **/
.suggest-box-container {
  width: 100%;
  display: block;
}

.suggest-box {
  width: calc(90%/3);
  margin-right:2.5%;
  border: 3px solid black;
  display: inline-block;
  padding: 20px;
}

.suggest-box img {
  display: block;
  width: 200px;
  height: 200px;
}

.suggest-box select, .suggest-box option{
  margin-top: 20px;
  display: inline-block;
  width: 100%;
}

/** button **/

.btn {
  display: block;
  background: rgba(0,0,0,.7);
  color: #fff;
  border: none;
  padding: 10px 20px;
  width: 75%;
  margin: 5px auto;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}
.btn:hover {
  background-color: rgba(0,0,0,1);
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

/*** form ***/

.form-close {
  position: relative;
  float: right;
  top: 0;
}

.form-group {
  margin: 20px 0;
  text-align: left;
}

label {
  text-align: left;
  font-weight: bold;
}

.form-group label {
  display: block;
  text-align: left;
}

.form-group input {
  margin-top: 10px;
  text-align: left;
  padding: 5px;
  width: 100%;
  border-radius: 3px;
  border: 1px solid #CCC;
}

.form-group select {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.container {
  
  border: 1px solid black;
  padding: 30px;
}

.container h2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.exit {
  cursor: pointer;
}

.backlog {
  width: 500px;
}

.qr-box {
  width: 150px;
  height: 150px;
}

.outcome {
  width: 100%;
  height: 100%;
}

.outcome-data {
  margin: 30px 0;
}

.outcome-images {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.outcome-images img {
  display: block;
  margin: 0 20px;
  height: 100px;
}

.full {
 width: 100%;
 height: 100%;
}

/***************/
/** Clue Page **/
/***************/

.clue-coloms {
  width: 100%;
}

.clue-coloms h2 {
  font-family: 'Chomsky', sans-serif;
  background-color: white;
  color: black;
  border-bottom: 2px black solid;
  text-align: left;
}

.clue-colom {
  float: left;
  overflow: visible;
  width: calc(100%/3);
}

.clue-colom li {
  list-style: none;
  text-align: left;
  cursor: pointer;
  padding: 3px 5px;
}

.clue-colom li.active {
  font-weight: bold;
}

.clue-colom li:hover {
  background-color: grey;
  color: white;
}

.clue-detail {

  width: 90%;
  margin-left: 5%;
  padding-bottom: 15px;
  min-height: 200px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.clue-detail-img-wrapper {
  float: left;
  text-align: center;
  width: 250px;
  min-width: 250px;
}

.clue-detail-img {
}

.detail-table {
  margin-left: 20px;
  float: left;
  word-break: break-all;
  width: calc(100% - 165px);
}

.detail-row th {
  text-align: left;
  vertical-align: text-top;
  width: 120px;
}

.detail-row td {
  text-align: left;
  padding: 2px;
}


/********************/
/** challenge list **/
/********************/

.challenges {
  margin: 15px 0;
  border: 2px solid black;
  width: 100%;
}

.challenge-list, .challenge-list thead, .challenge-list tr, .challenge-title {
  width: 100%;
}

.challenge-list tbody tr td{
  padding: 2px;
}

.challenge-title {
  font-family: sans-serif;
  background-color: black;
  color: aliceblue;
  font-size: 25px;
}

.challenge-item {
  text-align: left;
}

@media only screen and (max-width: 1050px) {
  
  h1 {
    font-size: 5rem;
  }
  
  .panel-1 {
    display: block;
    overflow: hidden;
    width: 100%;
  }
  
  .panel-2 {
    width: 100%;
  }
}

/************/
/** Cluedo **/
/************/

.cluedo-start {
  overflow: hidden;
  margin-left: calc(50% - 110px);
  border: 4px solid black;
  width: 220px;
  cursor: pointer;
  margin-top: 10%;
  
}

.cluedo-start:active {
  transform: translateY(5px);
}

.cluedo-start-img {
  height: 200px;
  margin: 10px;
  background: url('/src/Assets/images/flavicon.png');
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.cluedo-start p {
  background-color: black;
  color: aliceblue;
  font-size: 20px;
}

.mansion {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 60%;
}

.mansion-svg {
  position: absolute;
  top: 0;
  left: 0;
}

.mansion-svg .room {
  position: relative;
  cursor: pointer;
}

.mansion-svg .room .room-art {
  opacity: .2;
}

.mansion-svg .room .room-button {
  opacity: 0;
}

.mansion-svg .room:hover .room-art {
  opacity: .5;
  fill: #C00;
}

.mansion-svg .room .label {
  position: absolute;
  font-family: Arial, sans-serif;
  font-size: 10px;
}

.post-form, .cluedo-btn-box {
  margin: 20px -10px 0 -10px;
  display: flex;
}

.post-form, .cluedo-btn-box button {
  margin: 0 10px;
}

.no-base-url-message, .no-base-url-message * {
  text-align: left;
}

.no-base-url-message h3 {
  margin-bottom: 5px;
}

.no-base-url-message .btn {
  margin-top: 10px;
  display: inline-block;
  width: auto;
}

.api-form, .api-form form, .api-form, .api-form p {
  text-align: left;
  width: 100%;
}

.api-form .btn {
  display: inline-block;
  width: auto;
  margin: 0;
}